.loading{
    width: 100vw;
    height: 100vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
}
.loading > div{
width: 10vmax;
height: 10vmax;
border-bottom: 5px solid rgb(0, 0, 0, 0.719);
border-radius: 50%;
animation: loadingRotates 800ms linear infinite;
}

@keyframes loadingRotates{
    to {
        transform: rotateZ(-360deg);
    }
}