.confirmOrderPage{
    height: 100vh;
    background-color: white;
    display: grid;
    grid-template-columns: 6fr 3fr;
}

.confirmOrderPage > div:last-child{
    border-left: 1px solid rgba(0, 0, 0, 0.247);
}

.confirmShippingArea{
    padding: 5vmax;
    padding-bottom: 0%;
}
.confirmShippingArea > p{
    font: 400 1.8vmax "Roboto";
}
.confirmShippingAreaBox,
confirmCartItemContainer{
  margin: 2vmax;
}
.confirmShippingAreaBox > div{
    display: flex;
    margin: 1vmax 0;
}
.confirmShippingAreaBox > div > p{
    font: 400 1vmax "Roboto";
    color: black;
}

.confirmShippingAreaBox > div > span{
    margin: 0 1vmax;
    font: 100 1vmax "Roboto";
    color: #575757;
}

.confirmCartItems > p{
    font: 400 1.8vmax "Roboto";
}
.confirmCartItems{
    padding: 5vmax;
    padding-top: 2vmax;
}
.confirmCartItemContainer{
    max-height: 20vmax;
    overflow-y: auto;
}
.confirmCartItemContainer > div{
    display: flex;
    font: 400 1vmax "Roboto";
    margin: 2vmax 0;
    justify-content: space-between;
    align-items: center;
    margin: 2vmax 0;
}
.confirmCartItemContainer > div > img{
    width: 3vmax;
}
.confirmCartItemContainer > div > a{
   color: #575757;
   margin: 0 2vmax;
   width: 60%;
   text-decoration: none;
}

.confirmCartItemContainer > div > span{
    font: 100 1vmax "Roboto";
    color: #5e5e5e;
}

.orderSummary{
    padding: 7vmax;
}

.orderSummary > p{
    text-align: center;
    font: 400 1.8vmax "Roboto";
    border-bottom: 1px solid rgba(0, 0, 0, 0.267);
    padding: 1vmax;
    width: 100%;
    margin: auto;
    box-sizing: border-box;
}
.orderSummary > div > div{
    display: flex;
    justify-content: space-between;
    margin: 2vmax 0;
}

.orderSummary > div > div > span{
    color: rgba(0, 0, 0, 0.629);
}

.orderSummaryTotal{
    display: flex;
    border-top: 1px solid rgba(0, 0, 0, 0.363);
    padding: 2vmax 0;
    justify-content: space-between;
    font: 300 1vmax "Roboto";
}
.orderSummary > button{
background-color: tomato;
color: white;
width: 100%;
padding: 1vmax;
border: none;
margin: auto;
cursor: pointer;
transition: 0.5s;
font: 400 1vmax "Roboto";
}

.orderSummary > button:hover{
    background-color: rgb(192, 71, 50);
}

@media screen and (max-width: 600px){
    .confirmOrderPage{
        grid-template-columns: 1fr;
        height: unset;
    }
    .confirmOrderPage > div:last-child{
        border-left: 0;
        border-top: 1px solid rgba(0, 0, 0, 0.247);
    }

    .confirmShippingArea > p{
        font: 400 6vw "Roboto";
    }
    .confirmShippingAreaBox > div{
        display: flex;
        margin: 6vw 0;
    }
    .confirmShippingAreaBox > div > p{
        font: 400 4vw "Roboto";
    }
    .confirmShippingAreaBox > div > span{
        font: 100 4vw "Roboto";
    }
    .confirmCartItems > p{
        font: 400 6vw "Roboto";
    }
    .confirmCartItemContainer{
        max-height: 50vw;
    }
    .confirmCartItemContainer > div{
        font: 400 4vw "Roboto";
        margin: 4vw 0;
    }
   .confirmCartItemContainer > div > img{
    width: 10vw;
   }
   .confirmCartItemContainer > div > a{
    margin: 0;
    width: 30%;
   }
   .confirmCartItemContainer > div > span{
    font: 100 4vw "Roboto";
   }
   .orderSummary{
    padding: 12vw;
   }
   .orderSummary > p{
    font: 400 6vw "Roboto";
    padding: 4vw;
   }
   .orderSummary > div > div{
    font: 300 4vw "Roboto";
   }
   .orderSummaryTotal{
    font: 300 4vw "Roboto";
    padding: 5vw 0;
   }
   .orderSummary > button{
    padding: 4vw;
    margin: 4vw auto;
    font: 400 4vw "Roboto";
   }
}































